<template>
<div class="contact">
    <h1>contact</h1>
    <div class="group">
        <transition-group tag="ul"
            appear
            @before-enter = 'beforeEnter'
            @enter = 'Enter'
            
            
            >
            <li class="inner" 
            v-for = "(icon , index) in icons"
            :data-index = "index"
            :key = "icon.title"
            >
                <div class="icon">
                    <font-awesome-icon :icon= "icon.icon" />
                <span> {{ icon.title }}</span>
                </div>
            </li>
        </transition-group>
    </div>
</div>
</template>



<script>
import { ref } from '@vue/reactivity'
import gsap from 'gsap';
export default {
    setup() {
        const icons = ref([
            {icon: 'fa-at' , title: 'by email'},
            {icon: 'fa-phone' , title: 'by phone'},
            {icon: 'fa-envelope' , title: 'by post'},
            {icon: 'fa-fire' , title: 'by smoke signal'}
        ]);


    const beforeEnter = (el) => {
        el.style.transform = 'translateY(100px)'
        el.style.opacity = 0
    }

    const Enter = (el , done ) => {
        gsap.to(el , {
            duration: .8,
            y: 0,
            opacity: 1,
            delay: el.dataset.index * .2
        })
    }


        return {icons , beforeEnter , Enter}
    }
}
</script>


<style scoped>
body {
    background-color: #eeeeee;
}

ul {
    display: flex;
    width: 415px;
    flex-wrap: wrap;
    margin: 30px auto ;
}

.inner {
    width: 200px;
    height: 180px;
    background-color: #FFF;
    margin: 3px auto;
    list-style: none;
    box-shadow: 1px 1px 5px .5px;
    border-radius: 10px;
}

.icon {
    margin: 50px auto;
}

svg {
    font-size: 30px;
}
span {
    display: block;
    margin-top: 15px;
}

</style>