<template>
    <input type="text" v-model="input" class="input" placeholder="add a new todo..." @keypress.enter="newTodo">
    <div class="content" v-if="texts.length">
    <transition-group tag="ul" name = "list" appear>
        <li class="todo" v-for="text in texts" :key = 'text.id' @click="removeItem(text.id)">
            {{ text.title }}
        </li>
    </transition-group>
    </div>
    <div class="no-content" v-else>
        <h4>No content to show...</h4>
    </div>

    
    
</template>

<script>
import { ref } from 'vue'

export default {
    setup(props , {emit}) {
    const input = ref('')
    const texts = ref([
        {title : 'Make a bed' , id : 1},
        {title: 'Play video games' , id : 2},
    ])

    const newTodo = () => {
        if (input.value) {
            let id = Math.random();
            texts.value = [{title: input.value , id}, ...texts.value];
            input.value = '';
        }else {
            emit('badValue');
        }
    }

    const removeItem = (id) => {
        texts.value = texts.value.filter(item => item.id !== id)

    }

    return{input , texts, newTodo , removeItem}
    }
}
</script>

<style>
.input {
    outline: none;
    width: 350px;
    padding: 9px;
    border: none;
    border-radius: 6px;
    margin-bottom: 30px;
}
ul {
    max-width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
}
.todo {
    max-width: 350px;
    list-style: none;
    margin : 10px auto;
    background-color: #FFF;
    border-radius: 10px;
    cursor: pointer;
    padding: 10px;
}

.list-enter-from,
.list-leave-to {
    opacity: 0;
    transform: scale(.6);
}
.list-enter-to,
.list-leave-from {
    opacity: 1;
    transform : scale(1);
}
.list-enter-active,
.list-leave-active {
    transition: all .5s ease;
}
list-move {
    transition: all .4s ease;
}
</style>